body {
  background-color: #faebd7;
  margin: 0;
  padding: 0;
}

h1 {
  justify-content: center;
  align-items: center;
  font-family: cursive;
  font-size: 70px;
  display: flex;
}

.searchBox {
  background: #2f3640;
  border-radius: 50px;
  justify-content: space-between;
  align-items: center;
  gap: 8px;
  max-width: 230px;
  margin: 200px auto 100px;
  display: flex;
  position: relative;
}

.searchButton {
  color: #fff;
  background: var(--gradient-2, linear-gradient(90deg, #2af598 0%, #009efd 100%));
  border: 0;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  transition: all .3s cubic-bezier(.23, 1, .32, 1);
  display: inline-block;
  position: absolute;
  right: 8px;
}

button:hover {
  color: #fff;
  background-color: #1a1a1a;
  transform: translateY(-3px);
  box-shadow: 0 10px 20px #00000080;
}

button:active {
  box-shadow: none;
  transform: translateY(0);
}

.searchInput {
  color: #fff;
  background: none;
  border: none;
  outline: none;
  padding: 24px 46px 24px 26px;
  font-size: 15px;
}

.app {
  text-align: center;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 1rem;
  display: flex;
}

.app-list {
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 5px;
  list-style: outside;
  display: flex;
}

.app-name {
  margin-bottom: 20px;
  font-size: 24px;
}

.app-box {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 40px;
  max-width: 100%;
  display: flex;
}

.app-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  font-size: 18px;
  display: flex;
}

.app-subtitle-info {
  color: #404040;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.app-clanguages {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.languages-subtitle {
  color: #404040;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.app-image img {
  border-radius: 8px;
  max-width: 100%;
  height: auto;
}
